import React from 'react'
import { isArray } from 'lodash'

import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Text from '../../components/Text'
import Container from '../../components/Container'
import Image from '../../components/Image'
import Footnote from '../../components/Footnote'
import { responsive, Media } from '../../components/ThemeProvider/theme'

import five from './five.svg'
import abundant from './abundant.svg'
import income from './income.svg'
import power from './power.svg'
import five1 from './five1.svg'
import five2 from './five2.svg'
import abundant1 from './abundant1.svg'
import power1 from './power1.svg'
import powerMobile from './power-mobile.svg'
import income1 from './income1.svg'
import income2 from './income2.svg'
import pig from './pig.svg'
import people from './people.svg'

const secs = [
  {
    color: 'green',
    icon: five,
    bigTitle: '5',
    title: '5',
    desc: '54+1',
    pictures: [five1, five2],
    source: '*2020/12/31 '

  },
  {
    color: 'yellow',
    icon: power,
    bigTitle: '',
    title: '',
    desc: '',
    pictures: [[powerMobile, power1]],
    source: '2020/12/31'
  },
  {
    color: 'lightRed',
    icon: abundant,
    bigTitle: '',
    title: '',
    desc: '',
    pictures: [abundant1],
    source: ' : 2020/12/31',
    afterNote: ''
  },
  {
    color: 'purple',
    icon: income,
    bigTitle: '',
    title: '',
    desc: '',
    pictures: [income1, income2],
    source: 'Bloomberg2007/10/31~2020/12/312020/12/31',
    beforeNote: '* 2007/10/31  **iBoxx'
  },
]

const NoteText = ({ children, ...props }) => (
  <Text.Inline color="noteGreen" fontWeight="bold" {...props}>{children}</Text.Inline>
)

const Sections = () => {
  return (
    <Container id="fund-head" pt={responsive('2em', '3em')}>
      <Text.H2 borderBottom="1px solid" borderColor="darkBlue" mb="1em" pb="0.5em" color="darkBlue" fontWeight="bold">
        <Text.Inline></Text.Inline>
        {secs.map(({ color, bigTitle }) => (
          <Text.Inline color={color}>{bigTitle}</Text.Inline>
        ))}
        <Text.Inline ml="0.125em"></Text.Inline>
      </Text.H2>
      <Text mb="1em">
        5
      </Text>
      {secs.map(({ color, icon, title, desc, pictures, source, afterNote, beforeNote }, index) => (
        <Box position="relative" pt={index ? responsive('3em', '5em') : '1em'} pb="1em" key={index}>
          <Flex alignItems="center">
            <Box width={responsive('2.5rem', '4rem')} mr="0.25em" as="h2">
              <Image src={icon} />
            </Box>
            <Box
              flex={1}
              position="relative"
            >
              <Text.H2 color={color} fontWeight="bold">{title}</Text.H2>
              <Box position="absolute" left="0" right="0" bottom="-0.5em" transform="translateY(100%)" mt="0.5em">
                <Text>{desc}</Text>
              </Box>
            </Box>
            {index === 1 && (
              <Box width="15%" as={Media} greaterThan="mobile">
                <Box pl="1em">
                  <Image src={people} />
                </Box>
              </Box>
            )}
          </Flex>
          <Box pl={responsive('0', '4.5em')} pt="1em">
            <Flex py="3em" flexDirection={responsive('column', 'row')}>
              {pictures.map((src, k) => (
                <Box width="100%" pt="1em" key={k}>
                  {isArray(src) ? (
                    <>
                      <Media at="mobile">
                        <Image src={src[0]} />
                      </Media>
                      <Media greaterThan="mobile">
                        <Image src={src[1]} />
                      </Media>
                    </>
                  ) : <Image src={src} />}
                </Box>
              ))}
            </Flex>
            <Box width={!Boolean(index) && responsive('100%', '85%')}>
              <Footnote>
                <NoteText>{afterNote}</NoteText>
                {source}
                <NoteText>{beforeNote}</NoteText>
              </Footnote>
            </Box>
          </Box>
          {!Boolean(index) && (
            <Media greaterThan="mobile">
              <Box width="10%" position="absolute" right={responsive(0 , '1em')} bottom="-2em">
                <Image src={pig} />
              </Box>
            </Media>
          )}
        </Box>
      ))}
    </Container>
  )
}

export default Sections
